@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  background-color: rgb(226, 226, 226);
  width: 100%;
}

.navbar {
  background-color: black;
  color: white;
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 2;
  font-family: "Montserrat", sans-serif;
  justify-content: space-between;
}

.title {
  letter-spacing: 4px;
  left: 5vw;
  position: relative;
}

.button > button {
  color: white;
}

.buttons {
  display: flex;
  padding-right: 2vw;
  justify-content: center;
  align-items: center;
}

.directors_container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px 10px;
  margin: 10px;
}

.director {
  width: 100%;
  height: 60vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.director:hover {
  box-shadow: 0 0 10px black;
}

.film__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.film {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 23vw;
  height: 60vh;
  margin: 10px;
}

.film:hover {
  box-shadow: 0 0 10px black;
}

.film__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: 10px;
}
.SelectedDirectorFilm {
  display: flex;
  justify-content: center;
  align-items: center;
}
.directorFilm__page {
  background-color: rgb(226, 226, 226);
  height: 150vh;
}

.directorFilm {
  width: 23vw;
  height: 60vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.modal__container {
}

.modal__image {
  width: 42vw;
  height: 80vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.modal__container {
  width: 100vw;
  background-color: rgba(14, 13, 13, 0.897);
  margin: 20px;
  display: flex;
}

.modal__words {
  display: flex;
  flex-direction: column;
  color: white;
  width: 100%;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 50px;
}

.form__container {
  width: 95vw;
  background-color: rgba(14, 13, 13, 0.897);
  color: white;
  margin: 20px;
  display: flex;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.form__container > textarea {
  width: 80%;
  margin: 20px;
  resize: none;
  overflow: scroll;
}

